<style>
</style>

<template>
  <div class="hello">
    <h2>Stock Predict</h2>
    <!-- <el-input v-model="inputStockCode" placeholder="請輸入股票代號" style="display:inline-table; width: 30%; float:left"></el-input> -->
    <el-select v-model="inputStockCode" filterable placeholder="請輸入股票代號" multiple allow-create style="float:left; width:300px">
      <el-option
        v-for="item in options"
        :key="item.value"
        :label="item.value + '-' + item.label"
        :value="item.value">
      </el-option>
    </el-select>
    <el-button type="primary" @click="queryStock()" style="float:left; margin: 0px 0px 5px 5px;">查詢</el-button>

    <el-table
      :data="tableData"
      style="width: 100%"
      :header-cell-style="headerStyle"
      border
      :default-sor="{prop:'stockId',order:'asending'}">
      <el-table-column
        :prop="index"
        :label="item"
        show-overflow-tooltip
        v-for="(item, index) in tableHeader"
        :width="headerWidth[index]"
        sortable
        :key="index">
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: 'StockPredict',
  data () {
    return {
      options: [],
      msg: '',
      inputStockCode: '', // 使用者輸入股票代號
      tableHeader: {
        stockId: '股票代號',
        latestPrice: '最新股價',
        latestDate: '最新日期',
        reasonPrice_E: '合理價(E)',
        disasterPrice_E: '股災價(E)',
        lower_reason_E: '低於合理(E)',
        lower_disaster_E: '低於股災(E)',
        reasonPrice: '合理價(N)',
        disasterPrice: '股災價(N)',
        lower_reason: '低於合理(N)',
        lower_disaster: '低於股災(N)'
      },
      headerWidth: {
        stockId: '145%',
        latestPrice: '145%',
        latestDate: '145%',
        reasonPrice_E: '138%',
        disasterPrice_E: '135%',
        lower_reason_E: '135%',
        lower_disaster_E: '135%',
        reasonPrice: '135%',
        disasterPrice: '135%',
        lower_reason: '135%',
        lower_disaster: '135%'
      },
      tableData: []
    }
  },
  mounted: function () {
    window.vm = this // F12可console查參數
    this.getStockList() // 取得股票清單
  },
  methods: {
    // 取得股票代號清單
    getStockList () {
      // this.$http.get('http://127.0.0.1:8000/test2023/stockSelectList')
      this.$http.get('https://stockpredict.onrender.com/test2023/stockSelectList')
        .then((response) => {
          var res = JSON.parse(response.bodyText)
          if (res.error_num === 0) {
            this.options = []
            res.result.forEach(param => {
              let map = {'value': param.pk, 'label': param.fields.stockName}
              this.options.push(map)
            })
            // this.options = res.result
          } else {
            this.$message.error('取得股票代號清單失敗')
            console.log(res['msg'])
          }
        })
    },
    // 回傳header樣式
    headerStyle ({row, rowIndex}) {
      return 'background-color: #ECF5FF;'
    },
    queryStock () { // 查詢股票
      this.transParam()
    },
    transParam () { // 呼叫股價預測api
      // this.$http.get('http://127.0.0.1:8000/test2023/stock_predict?stockCode=' + this.inputStockCode)
      this.$http.get('https://stockpredict.onrender.com/test2023/stock_predict?stockCode=' + this.inputStockCode)
        .then((response) => {
          var res = JSON.parse(response.bodyText)
          console.log(res)
          this.tableData = []
          if (res.error_num === 0) {
            console.log('success')
            let result = res.list
            result.forEach(element => {
              let map = {}
              map.stockId = element[0]
              map.latestPrice = element[1]
              map.latestDate = element[2]
              map.reasonPrice_E = element[3]
              map.disasterPrice_E = element[4]
              map.lower_reason_E = element[5].toString()
              map.lower_disaster_E = element[6].toString()
              map.reasonPrice = element[7]
              map.disasterPrice = element[8]
              map.lower_reason = element[9].toString()
              map.lower_disaster = element[10].toString()
              this.tableData.push(map)
            })
          } else {
            this.$message.error('失敗囉')
            console.log(res['msg'])
          }
        })
    }
  }
}
</script>



