<template>
  <div class="hello">
    <h2>Stock Predict</h2>
    <el-input v-model="input" placeholder="請輸入股票代號" style="display:inline-table; width: 30%; float:left"></el-input>
    <el-button type="primary" @click="addStock()" style="float:left; margin: 2px;">新增</el-button>

    <el-table
      :data="tableData"
      style="width: 100%"
      :default-sor="{prop:'stockId',order:'asending'}">
      <el-table-column
        :prop="index"
        :label="item"
        v-for="(item, index) in tableHeader"
        sortable
        show-overflow-tooltip
        style="width:9vw"
        :key="index">
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: '',
      input: '',
      tableHeader: {
        stockId: '股票代號',
        latestPrice: '最新股價',
        latestDate: '最新日期',
        reasonPrice_E: '易受影響股票合理價',
        disasterPrice_E: '易受影響股票股災價',
        lower_reason_E: '是否低於合理',
        lower_disaster_E: '是否低於股災',
        reasonPrice: '不受影響股票合理價',
        disasterPrice: '不受影響股票股災價',
        lower_reason: '是否低於合理',
        lower_disaster: '是否低於股災'
      },
      tableData: []
    }
  },
  mounted: function () {

  },
  methods: {
    addStock () {
      this.transParam()
    },
    transParam () {
      this.$http.get('http://127.0.0.1:8000/test2023/stock_predict?stockCode=' + this.input)
        .then((response) => {
          var res = JSON.parse(response.bodyText)
          console.log(res)
          this.tableData = []
          if (res.error_num === 0) {
            let result = res.list
            result.forEach(element => {
              let map = {}
              map.stockId = element[0]
              map.latestPrice = element[1]
              map.latestDate = element[2]
              map.reasonPrice_E = element[3]
              map.disasterPrice_E = element[4]
              map.lower_reason_E = element[5].toString()
              map.lower_disaster_E = element[6].toString()
              map.reasonPrice = element[7]
              map.disasterPrice = element[8]
              map.lower_reason = element[9].toString()
              map.lower_disaster = element[10].toString()
              this.tableData.push(map)
            })
          } else {
            this.$message.error('失敗囉')
            console.log(res['msg'])
          }
        })
    }
  }
}
</script>



